import React from "react";
import "./Characteristics.css";

function Characteristics() {
  return (
    <section id="characteristics" className="characteristics">
      <div className="container">
        <h4 className="characteristics__maintitle">Характеристики</h4>
        <div className="characteristics__inner">
          <div className="characteristics__items">
            <ul className="characteristics__list">
              <li className="characteristics__title">Общие</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Производитель</p>
                <p className="characteristics__desc">Xiaomi</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Вид</p>
                <p className="characteristics__desc">
                  взрослый, дорожный, городской, электровелосипед, алюминиевая
                  рама
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Колеса</p>
                <p className="characteristics__desc">
                  26, двойной алюминиевый обод
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Электропривод</p>
                <p className="characteristics__desc">
                  мотор 250W, разгон до 25 км/ч
                </p>
              </li>
            </ul>
            <ul className="characteristics__list">
              <li className="characteristics__title">амортизация</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Тип амортизации</p>
                <p className="characteristics__desc">
                  амортизирующая вилка (Hard tail)
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Вилка</p>
                <p className="characteristics__desc">пружинно-эластомерная</p>
              </li>
            </ul>
            <ul className="characteristics__list">
              <li className="characteristics__title">управление</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Тип амортизации</p>
                <p className="characteristics__desc">
                  амортизирующая вилка (Hard tail)
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Вилка</p>
                <p className="characteristics__desc">пружинно-эластомерная</p>
              </li>
            </ul>
            <ul className="characteristics__list">
              <li className="characteristics__title">управление</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Руль</p>
                <p className="characteristics__desc">
                  изогнутый, регулируемый подъем
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Манетки</p>
                <p className="characteristics__desc">
                  триггерные однорычажные, Shimano
                </p>
              </li>
            </ul>
            <ul className="characteristics__list">
              <li className="characteristics__title">тормоза</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Передний</p>
                <p className="characteristics__desc">дисковый механический</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Задний</p>
                <p className="characteristics__desc">дисковый механический</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">
                  Готовность к установке дисковых
                </p>
                <p className="characteristics__desc">рама, вилка, втулка </p>
              </li>
            </ul>
            <ul className="characteristics__list">
              <li className="characteristics__title">
                характеристика трансмиссии
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Количество скоростей</p>
                <p className="characteristics__desc">7</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Педали</p>
                <p className="characteristics__desc">классические</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Передний переключатель</p>
                <p className="characteristics__desc">1 звезда</p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Задний переключатель</p>
                <p className="characteristics__desc">
                  Shimano Tourney (прогулочный)
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Кассета</p>
                <p className="characteristics__desc">7 звезд</p>
              </li>
            </ul>

            <ul className="characteristics__list">
              <li className="characteristics__title">прочее</li>
              <li className="characteristics__line">
                <p className="characteristics__name">Оснащение</p>
                <p className="characteristics__desc">
                  электрооборудование, звонок
                </p>
              </li>
              <li className="characteristics__line">
                <p className="characteristics__name">Вес</p>
                <p className="characteristics__desc">25 кг</p>
              </li>
            </ul>
          </div>
          <img
            src="images/characteristics.jpg"
            alt=""
            className="characteristics__img"
          />
        </div>
      </div>
    </section>
  );
}

export default Characteristics;
