import React from "react";

import Slider from "react-slick";
import AnchorLink from "react-anchor-link-smooth-scroll";
import "./Bike.css";
function Bike() {
  const settings = {
    dots: true,
    arrows: false,
    fade: true,
    infinite: true,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 2000,
    cssEase: "linear",
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <section className="bike">
      <div className="container">
        <div className="bike__inner">
          <div className="bike__column">
            <h1 className="bike__title">Xiaomi Himo C26</h1>
            <h3 className="bike__subtitle">Электрический велосипед</h3>
            <p className="bike__text">
              Полная свобода передвижения и надежный транспорт для путешествий
              на любые расстояния
            </p>
            <p className="bike__text">
              HIMO C26 – это новый электровелосипед от Xiaomi, который создан
              как для езды по городу, так и бездорожью. Он имеет мощный
              двигатель и емкий аккумулятор, благодаря которым способен
              проезжать на одном заряде до 100 км, что определенно выше, чем у
              большинства конкурентов. А большие колеса с пневматическими
              шинами, дисковые тормоза, 7-скоростная трансмиссия и эргономичное
              сидение делают каждую поездку гораздо приятнее.{" "}
            </p>
            <AnchorLink className="bike__link" href="#price">
              ЗАКАЗАТЬ
            </AnchorLink>
          </div>
          <div className="bike__column bike__column-slider">
            <Slider className="bike__slider" {...settings}>
              <img
                src="images/bike-1.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-2.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-3.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-4.jpg"
                alt=""
                className="bike-slider__item"
              />
            </Slider>
            <ul className="bike__list">
              <li className="bike__list-item">
                <span className="bike__list-num"> до 100 км</span>{" "}
                <span className="bike__list-text">Запас хода</span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">25 км/ч </span>{" "}
                <span className="bike__list-text">Макс корость</span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">3 режима езды </span>
                <span className="bike__list-text">
                  механический, гибридный, электрический
                </span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">25 кг </span>{" "}
                <span className="bike__list-text">вес</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Bike;
